*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

p {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

address {
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
}

article {
  background: none;
  border: 0;
  outline: 0;
  margin: 0;
  padding: 0;
  font-size: 100%;
  display: block;
}

:root {
  --title-font-family: "Poppins", sans-serif;
  --primary-font-family: "Poppins", sans-serif;
  --gallery-font-family: "Roboto", sans-serif;
  --light-bg-color: #fcfcfc;
  --dark-bg-color: #202025;
  --white-color: #fcfcfc;
  --black-color: #000;
  --medium-grey-color: #5f6775;
  --title-light-color: #202025;
  --title-dark-color: #fcfcfc;
  --filter-letter-light-color: #5f6775;
  --filter-letter-dark-color: #5f6775;
  --modal-text-light-color: #5f6775;
  --modal-text-dark-color: #fcfcfc;
  --modal-title--black: #000917;
  --btn-bg-color: #fe7031;
  --btn-inmodal-bg-color: #fd5103;
  --btn-text-color: #fd5103;
  --btn-outline-color: #ff8650;
  --pretitle-hero-color: #fd5103;
  --accent-color: #fd5103;
  --btn-hover-color: #fd5103;
  --card-item: 1;
  --card-gap: 20px;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --body-background-color: #fff;
  --accent-theme-color: #fd5103;
  --theme-dark-color: #5f6775;
  --manu-theme-color: #202025;
  --search-input-color: #fff;
  --input-text-color: #202025;
  --btn-mob-menu-color: #202025;
  --fav-dropdown-item: #202025;
  --hero-text-color: #202025;
  --hero-bg-input-color: #fcfcfc;
  --select-input-color: #fcfcfc;
  --gallery-btn-fav-color: #fff;
  --gallery-title-color: #000;
  --gallery-subtitle-color: #5f6775;
  --modal-bg-color: #fff;
  --modal-title-color: #000917;
  --modal-text-color: #5f6775;
  --modal-accent-text-color: #000;
  --background-color: #00000080;
  --modal-close-icon-color: #202025;
  --mob-menu-bg-color: #fcfcfc;
  --menu-mob-color: #202025;
  --heart-color: #fcfcfc;
}

.dark-theme {
  --body-background-color: #202025;
  --title-light-color: #fcfcfc;
  --accent-theme-color: #fcfcfc;
  --theme-dark-color: #fd5103;
  --manu-theme-color: #fcfcfc;
  --search-input-color: #202025;
  --input-text-color: #fcfcfc;
  --btn-mob-menu-color: #fcfcfc;
  --fav-dropdown-item: #fcfcfc;
  --hero-text-color: #fcfcfc;
  --hero-bg-input-color: #202025;
  --select-input-color: #202025;
  --gallery-btn-fav-color: #202025;
  --gallery-title-color: #fcfcfc;
  --gallery-subtitle-color: #fcfcfc;
  --modal-bg-color: #202025;
  --modal-title-color: #fcfcfc;
  --modal-text-color: #bdbdbd;
  --modal-accent-text-color: #fcfcfc;
  --background-color: #fff3;
  --modal-close-icon-color: #fcfcfc;
  --mob-menu-bg-color: #202025;
  --menu-mob-color: #fcfcfc;
  --heart-color: #202025;
}

html {
  scroll-behavior: smooth;
}

body {
  min-height: 100%;
  background-color: var(--body-background-color);
  box-sizing: border-box;
  margin: 0;
  font-style: normal;
  font-weight: 500;
}

.overflow-hidden {
  overflow: hidden;
}

.page {
  min-height: 100vh;
  flex-direction: column;
  display: flex;
}

footer {
  margin-top: auto;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 22px;
  font-weight: 600;
  line-height: 1.91;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.card-set {
  gap: var(--card-gap);
  flex-wrap: wrap;
  display: flex;
}

.card-set-item {
  flex-basis: calc((100% - (var(--card-item)  - 1) * var(--card-gap)) / var(--card-item));
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media (min-width: 320px) {
  .container {
    width: 320px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.section {
  padding-top: 60px;
  padding-bottom: 40px;
}

@media (min-width: 768px) {
  .section {
    padding-top: 68px;
    padding-bottom: 60px;
  }
}

@media (min-width: 1280px) {
  .section {
    padding-top: 37px;
    padding-bottom: 60px;
  }
}

.section__title {
  text-align: center;
  color: var(--title-color);
  font-family: var(--title-font-family);
}

@media (min-width: 768px) {
  .section__title {
    font-weight: 700px;
    font-size: 32px;
    line-height: 1.31;
  }
}

@media (min-width: 1280px) {
  .section__title {
    font-size: 48px;
    line-height: 1.5;
  }
}

.btn {
  text-align: center;
  font-family: inherit;
  font-style: inherit;
  border-width: 0;
  border-color: #0000;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  display: inline-flex;
}

#runaway-btn {
  min-height: 50px;
  min-width: 200px;
  color: var(--white-color);
  box-shadow: 1px 1px 5px var(--white-color);
  background-color: var(--accent-color);
  border: none;
  border-radius: 5px;
  font-size: 22px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (max-width: 1279.98px) {
  #runaway-btn {
    display: none;
  }
}

.backdrop__mobile {
  z-index: 15;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  align-items: center;
  position: fixed;
  top: 0;
  right: 0;
}

@media (min-width: 320px) {
  .backdrop__mobile {
    -webkit-backdrop-filter: blur(4.5px);
    backdrop-filter: blur(4.5px);
    -webkit-backdrop-filter: blur(4.5px);
    background: #ffffff40;
    flex-direction: column;
    align-items: flex-start;
    box-shadow: 20px 20px 40px -6px #0003;
  }
}

.backdrop__mobile.is-open {
  overflow-y: none;
  display: flex;
}

.backdrop__mobile.is-hidden {
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  overflow-y: none;
}

.menu__container {
  width: 100vw;
  height: 70%;
  background-color: var(--mob-menu-bg-color);
  transition: all var(--menu-transition);
  flex-direction: column;
  padding: 32px 20px;
  display: flex;
  position: relative;
}

.btn__burger-menu-toggle {
  z-index: 25;
  width: 40px;
  height: 40px;
  fill: var(--menu-mob-color);
  transition: all var(--menu-transition);
  background-color: #0000;
  border-radius: 50%;
  margin-left: auto;
  padding: 0;
  display: flex;
  position: static;
}

@media (min-width: 1280px) {
  .btn__burger-menu-toggle {
    display: none;
  }
}

.btn__burger-menu-toggle .menu__open-icon {
  opacity: 1;
}

.btn__burger-menu-toggle .menu__close-icon, .btn__burger-menu-toggle.is-active .menu__open-icon {
  opacity: 0;
}

.btn__burger-menu-toggle.is-active .menu__close-icon {
  opacity: 1;
}

.btn__burger-menu-toggle:hover, .btn__burger-menu-toggle:focus {
  filter: drop-shadow(-2px 2px 2px #6f6e6e);
}

.menu-toggle__icon {
  transition: all var(--menu-transition);
  fill: var(--btn-mob-menu-color);
}

.btn__burger-menu-toggle:hover svg, .btn__burger-menu-toggle:focus svg {
  fill: var(--accent-color);
}

.menu__header {
  align-items: flex-start;
  display: flex;
}

@media (min-width: 768px) and (max-width: 1279.98px) {
  .menu__header {
    display: flex;
  }
}

.menu__logo {
  transition: all var(--menu-transition);
  display: none;
}

.menu__logo:hover, .menu__logo:focus {
  filter: drop-shadow(-2px 2px 2px #6f6e6e);
}

@media (min-width: 768px) and (max-width: 1279.98px) {
  .menu__logo {
    font-family: var(--title-font-family);
    letter-spacing: .02em;
    color: var(--accent-color);
    margin-right: 40px;
    font-size: 22px;
    font-weight: 400;
    line-height: 1.1;
    display: flex;
  }
}

.menu__list {
  color: var(--hero-text-color);
  flex-direction: column;
  font-size: 28px;
  font-weight: 400;
  line-height: .86;
  display: flex;
}

.menu__theme-list {
  gap: 8px;
  margin-bottom: 40px;
  margin-right: 10px;
  display: flex;
}

.menu__theme-item {
  display: flex;
}

.menu__theme-light {
  font-family: var(--title-font-family);
  color: var(--accent-theme-color);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.71;
}

.menu__switcher {
  width: 40px;
  height: 20px;
  background: #fff;
  border: .604701px solid #fd5103;
  border-radius: 20px;
}

.menu__theme-dark {
  font-family: var(--title-font-family);
  color: var(--theme-dark-color);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.71;
}

.menu__login-btn {
  font-family: var(--title-font-family);
  background-color: var(--accent-color);
  color: var(--body-background-color);
  margin-left: 10px;
  padding: 3px 10px;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.33;
  display: flex;
  position: relative;
  top: -3px;
}

@media (min-width: 768px) {
  .menu__login-btn {
    margin-left: 40px;
    padding: 5px 15px;
  }
}

.menu__login-btn:hover, .menu__login-btn:focus {
  box-shadow: -2px 2px 4px #6f6e6e;
}

.menu__search {
  margin-bottom: 40px;
  position: relative;
}

@media (min-width: 768px) {
  .menu__search {
    display: none;
  }
}

.menu__search-icon {
  position: absolute;
  top: 20px;
  left: 8px;
  transform: translateY(-50%);
}

.menu__search-input {
  max-width: 90%;
  height: 40px;
  color: var(--white-color);
  background-color: var(--search-input-color);
  border: 1px solid #fd5103;
  border-radius: 5px;
  outline: none;
  padding: 8px 8px 8px 48px;
}

@media (min-width: 480px) {
  .menu__search-input {
    width: 50%;
  }
}

.menu__search-btn {
  width: 40px;
  height: 40px;
  background-color: #0000;
  border: 1px;
  position: absolute;
  left: 0;
}

.menu__search-border:after {
  content: "";
  width: 39px;
  height: 1px;
  background-color: var(--accent-color);
  position: absolute;
  top: 20px;
  left: 20px;
  transform: rotate(90deg);
}

.menu__nav-link {
  color: var(--menu-mob-colorg);
  background-color: #0000;
  border: 0;
}

@media (min-width: 768px) and (max-width: 1279.98px) {
  .menu__nav-list {
    flex-flow: column wrap;
    align-content: center;
    display: flex;
  }
}

.menu__nav-item {
  color: var(--menu-mob-color);
  transition: all var(--menu-transition);
  font-size: 28px;
  font-weight: 400;
  line-height: .86;
}

.menu__nav-item:before {
  content: "♥";
  color: #0000;
}

.menu__nav-item:not(:last-child) {
  margin-bottom: 32px;
}

.menu__nav-item:hover, .menu__nav-item:focus {
  color: var(--accent-color);
  filter: drop-shadow(-2px 2px 2px #6f6e6e);
}

.menu__fav-item:before {
  content: "♥";
  color: var(--accent-color);
}

.backdrop__cocktail {
  width: 100%;
  height: 100%;
  background: var(--background-color);
  position: fixed;
  top: 0;
  left: 0;
}

.modal__ingredient, .modal__cocktail {
  background: var(--modal-bg-color);
  overflow: overlay;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

.modal__cocktail {
  min-width: 320px;
  height: 600px;
}

@media (min-width: 768px) and (max-width: 1279.98px) {
  .modal__cocktail {
    width: 768px;
    height: 598px;
  }
}

@media (min-width: 1280px) {
  .modal__cocktail {
    width: 822px;
    height: 598px;
  }
}

.modal__ingredient {
  width: 320px;
  height: 600px;
}

@media (min-width: 768px) and (max-width: 1279.98px) {
  .modal__ingredient {
    width: 768px;
    height: 598px;
  }
}

@media (min-width: 1280px) {
  .modal__ingredient {
    width: 715px;
    height: 598px;
  }
}

@media (min-width: 768px) {
  .modal__wrap {
    align-items: flex-start;
    display: flex;
  }
}

.cocktail__img {
  width: 320px;
  height: 320px;
}

@media (min-width: 768px) {
  .cocktail__img {
    width: 288px;
    height: 320px;
  }
}

.cocktail__modal-btn {
  margin-bottom: 28px;
  margin-left: 32px;
}

@media (min-width: 768px) {
  .cocktail__modal-btn {
    justify-content: flex-end;
    margin-bottom: 28px;
    margin-right: 28px;
    display: flex;
  }
}

.modal__btn {
  text-align: center;
  height: 48px;
  font-family: var(--gallery-font-family);
  letter-spacing: .02em;
  color: var(--white-color);
  background: var(--btn-text-color);
  border-color: #0000;
  border-radius: 4px;
  margin-top: 20px;
  padding: 0 24px;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.2;
}

.modal__btn:hover, .modal__btn:focus {
  background: var(--btn-bg-color);
  color: var(--white-color);
}

.modal__btn:active {
  color: var(--btn-bg-color);
  background: var(--btn-bg-color);
}

.ingredient__name, .cocktail__name {
  font-family: var(--gallery-font-family);
  color: var(--modal-title-color);
  margin-top: 40px;
  margin-left: 32px;
  font-size: 32px;
  font-weight: 900;
  line-height: 1.2;
}

.ingredient__title, .cocktail__title {
  font-family: var(--primary-font-family);
  text-transform: uppercase;
  color: var(--modal-title-color);
  margin-top: 8px;
  margin-left: 32px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.5;
}

.cocktail__text {
  font-family: var(--primary-font-family);
  color: var(--modal-title-color);
  margin-top: 8px;
  margin-left: 32px;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.5;
}

.ingredient {
  height: 160px;
  max-width: 320px;
  margin-top: 8px;
  margin-bottom: 43px;
  margin-left: 32px;
}

@media (min-width: 1280px) {
  .ingredient {
    min-width: 430px;
    overflow: overlay;
  }
}

@media (min-width: 768px) and (max-width: 1279.98px) {
  .ingredient {
    min-width: 448px;
  }
}

.ingredient__item {
  font-family: var(--primary-font-family);
  color: var(--modal-text-color);
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;
}

.ingredient__accent {
  color: var(--modal-title-color);
}

.ingredient-link {
  cursor: pointer;
}

.ingredient-link:hover {
  color: var(--accent-color);
}

.recipe__wrap {
  margin-left: 28px;
}

.recipe__title {
  font-family: var(--primary-font-family);
  text-transform: uppercase;
  color: var(--modal-title-color);
  margin-top: 28px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.6;
}

.recipe__text {
  max-width: 722px;
  font-family: var(--primary-font-family);
  color: var(--modal-text-color);
  overflow: overlay;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.75;
}

@media (min-width: 768px) {
  .recipe__text {
    max-height: 90px;
  }
}

.btn--close {
  fill: var(--accent-color);
  background-color: #0000;
  border: #0000;
  justify-content: flex-end;
  align-items: flex-start;
  margin-left: auto;
  padding: 0;
  display: flex;
  position: absolute;
  top: 12px;
  right: 12px;
}

.accent__text {
  color: var(--modal-accent-text-color);
  font-weight: 600;
}

.description__text {
  max-width: 654px;
  font-family: var(--primary-font-family);
  color: var(--modal-text-color);
  margin-top: 28px;
  margin-left: 32px;
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
}

.characteristic {
  margin-top: 28px;
  margin-bottom: 40px;
  margin-left: 28px;
}

.view {
  margin-bottom: 40px;
}

.line {
  border-bottom: 1px solid var(--accent-color);
  min-width: 320px;
}

@media (min-width: 1280px) {
  .line {
    min-width: 715px;
  }
}

.backdrop__cocktail ::-webkit-scrollbar {
  width: 4px;
  border-radius: 4px;
}

.backdrop__cocktail ::-webkit-scrollbar-track {
  background: #e0e0e0;
}

.backdrop__cocktail ::-webkit-scrollbar-thumb {
  background: #fd5103;
  border-radius: 4px;
}

.ing_card_wrap {
  align-items: center;
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
}

.backdrop-sign-in {
  width: 100%;
  height: 100%;
  background-color: #0003;
  transition: opacity .25s linear, visibility .25s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop-sign-in .visually-hidden .modal {
  transform: translate(-50%, -50%)scale(0)rotate(-180deg);
}

.modal {
  transform: translate(-50%) scale(1) rotate(0);
  background-color: #fff;
  border-radius: 20%;
  padding: 40px;
  transition: all cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 93px;
  left: 50%;
}

@media screen and (min-width: 768px) {
  .modal {
    top: 208px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    padding: 50px 40px;
    top: 221px;
  }
}

